<template>
    <div class="anec__wall">
        <div class="wall__post" v-for="(post, key) in posts" :key="key">
            <img :src="post.content" alt="post" class="post__content">
            <div class="post__reactions">
                <div class="reactions__likes">
                    <div class="likes container">
                        <button class="likes button"></button>
                        <p class="likes text">{{ post.likes }}</p>
                    </div>
                    <div class="dislikes container">
                        <button class="dislikes button"></button>
                        <p class="dislikes text">{{ post.dislikes }}</p>
                    </div>
                </div>
                <div class="reactions__coments container">
                    <p class="reactions__coments text">{{ post.coments }}</p>
                    <button class="reactions__coments button"></button>
                </div>
            </div>
        </div>
        <div class="wall__paging container">
            <button class="wall__paging button">{{ "<<" }}</button>
                <p class="wall__paging text" v-for="(page, key) in [1, 2, 3, 4, 5, 6]" :key="key">{{ page }}</p>
            <button class="wall__paging button">{{ ">>" }}</button>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            posts: [
                {
                    content: 'anecs/content1.png',
                    likes: 50,
                    dislikes: 10,
                    coments: 10
                },
                {
                    content: 'anecs/content2.png',
                    likes: 100,
                    dislikes: 30,
                    coments: 20
                },
                {
                    content: 'anecs/content3.png',
                    likes: 40,
                    dislikes: 5,
                    coments: 5
                },
                {
                    content: 'anecs/content4.png',
                    likes: 189,
                    dislikes: 30,
                    coments: 29
                },
            ]
        }
    }
}
</script>

<style>
.anec__wall {
    max-width: 952px;
    background-color: #FFFFF0;
    padding: 0 40px;
}

.post__content {
    width: 100%;
}

.post__reactions {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}

.post__reactions .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 7px;
}

.post__reactions .button {
    border: none;
    background-color: transparent;
}

.reactions__likes {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
}

.reactions__likes .button {
    width: 55px;
    height: 55px;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
}

.likes.button {
    background-image: url('/public/likes.svg');
}

.dislikes.button {
    background-image: url('/public/dislikes.svg');
}

.reactions__coments.button {
    width: 75px;
    height: 75px;
    background-image: url('/public/coments.svg');
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
}

.wall__paging.container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 540px;
    margin: 141px auto 76px;
}

.wall__paging.button {
    width: 53px;
    height: 44px;
    background-color: #FFF;
    border: 1px solid #000;
}

.wall__paging.text {
    font-family: Inter;
    font-size: 36px;
    font-weight: 500;
    line-height: 43.57px;
}

@media screen and (width <= 769px) {
    .anec__wall {
        max-width: 690px;
        padding: 0 15px;
    }
}

@media screen and (width <= 577px) {
    .reactions__likes .button {
        width: 39px;
        height: 39px;
    }

    .reactions__coments.button {
        width: 39px;
        height: 39px;
    }

    .wall__paging.container {
        margin: 74px auto 14px;
    }

    .wall__paging.text {
        font-family: Inter;
        font-size: 20px;
        font-weight: 500;
        line-height: 24.2px;
    }
}
</style>